@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap');

* {
  box-sizing:border-box;
  font-family: 'Roboto', sans-serif;
}
.App {
  display: flex;
  text-align: center;
  width: 100vw;
  max-height: 100vh;
  height: 100%;
}
.main {
  overflow: hidden;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}